import React, { useEffect } from 'react'
import { useHistory, Link } from "react-router-dom";


const MultipleURLChange = () => {
  const urlParams = new URLSearchParams(window.location.search);
  const delayTime = urlParams.get('delayTime') || 2000;
  const MAX_COUNTER = 50

  const history = useHistory();

  const handleMultipleRequest = ((currentCounter) => {
    const urls = []
    for (let i = currentCounter; i <= currentCounter + 3; i ++) {
      const delay = Math.ceil(delayTime / 1000);
      urls.push(`https://reqres.in/api/users/${i}?delay=${delay}`)
    }
    Promise.all(urls.map(u=>fetch(u)))
      .then(() => console.log('done'))
      .catch((err) => console.error(err))
      .finally(() => {
        if (currentCounter < MAX_COUNTER) {
          handleMultipleRequest(currentCounter + 1)
        }
      })
  })

  const updateURLs = () => {
    history.push({ search: '?color=red' })
    setTimeout(() => {
      history.push({ search: '?color=blue' })
    }, 500)
    setTimeout(() => {
      history.push({ search: '?color=black' })
    }, 2000)
  }

  useEffect(() => {
    updateURLs()
    setTimeout(() => {
      handleMultipleRequest(1)
    }, 1000)
  }, []) // eslint-disable-line

  return (
    <section id="login-section">
      <h3>
        Multiple URL Change-1
      </h3>
      <div>
        <Link to="/multiple-2" id="multiple-2">Multiple URL - 2</Link>
      </div>
    </section>
  );
};

export default MultipleURLChange;
